	module.exports = {"data":{"markdownRemark":{"frontmatter":{"block":"Responsive Web Design Principles"},"html":"<h2>Introduction to the Responsive Web Design Challenges</h2>\n<p>如今，从大屏的台式电脑到小屏的手机，我们有各式各样可以上网的设备。</p>\n<p>这些设备有着不同的屏幕尺寸、分辨率和处理能力。</p>\n<p>响应式 Web 设计是一种能自动响应不同尺寸设备的设计方法。</p>\n<p>实现这一切的核心要素是：媒介查询（Media Query）。</p>\n<p>如果你预计网站的大部分流量来自移动端，那么应该采取“移动端优先”的策略，再为PC端做兼容。</p>\n<p>如果你预计网站的大部分流量来自PC端，那么应该采取“PC端优先”的策略，再为移动端做兼容。</p>\n<p>CSS 提供了书写不同样式规则的工具，然后根据显示网页的设备来应用这些样式。</p>\n<p>本章介绍了如何使用媒介查询来实现响应式 Web 设计。</p>"},"allChallengeNode":{"edges":[{"node":{"fields":{"slug":"/responsive-web-design/responsive-web-design-principles/create-a-media-query"},"title":"Create a Media Query"}},{"node":{"fields":{"slug":"/responsive-web-design/responsive-web-design-principles/make-an-image-responsive"},"title":"Make an Image Responsive"}},{"node":{"fields":{"slug":"/responsive-web-design/responsive-web-design-principles/use-a-retina-image-for-higher-resolution-displays"},"title":"Use a Retina Image for Higher Resolution Displays"}},{"node":{"fields":{"slug":"/responsive-web-design/responsive-web-design-principles/make-typography-responsive"},"title":"Make Typography Responsive"}}]}},"pathContext":{"block":"responsive-web-design-principles","slug":"/responsive-web-design/responsive-web-design-principles"}}

